.header {
  height: 80px;
  background-color: #f2f2f2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 5%;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.logo {
  background-image: url(../../../public/logo.png);
  background-size: contain;
  height: 100px;
  width: 200px;
  margin-right: 80px;
}
.header-nav {
  list-style-type: none;
  display: flex;
  align-items: center;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
}
.nav-link,
.last-link {
  text-decoration: none;
  color: black;
}
.nav-link::after {
  content: '|';
  padding: 0 32px;
  font-size: 24px;
  line-height: 28px;
  font-weight: 100;
}
.last-link::after {
  content: '';
  padding: 0 32px;
  font-size: 24px;
  line-height: 28px;
  font-weight: 100;
}
.header-btns {
  display: flex;
}
.header-btn {
  background-color: #056cf2;
  width: 139px;
  height: 50px;
  color: #fff;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  border: none;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  transition: .2s;
  cursor: pointer;
}
.header-btn:hover {
  background-color: #448FF2;
}
.header-btn:first-child {
  margin-right: 25px;
}
.btn-icon-sign-in {
  background-image: url(../../../public/sign-in.png);
  background-repeat: no-repeat;
  background-size: contain;
  height: 28px;
  width: 25px;
}
.btn-icon-lang {
  background-image: url(../../../public/lang.png);
  background-repeat: no-repeat;
  background-size: contain;
  width: 35px;
  height: 29px;
}
.active-link {
  color: #056cf2;
}
.active-link::after {
  color: #000;
}